$main: #ddd;
$compass-primary: #fa752a;
$compass-secondary: #ee8d5c;

header {
  width: 100%;
  height: 70px;
  background: $main;
  display: flex;
  justify-content: stretch;

  .menu {
    flex: 10;
    display: flex;
    justify-content: space-around;
  }

  .logout {
    flex: 1;

    #logoutBtn {
      color: red;

      width: 100%;
      height: 100%;

      svg {


        width: 3em;
        height: 3em;
      }
    }

  }
}