.lesson-view-header {
  .menu-item {
    padding: 10px;
    margin: 10px 20px;
  }

  .underline {
    position: relative;

    a {
      text-align: left;
      font-family: 'Roboto', sans-serif;
      font-size: 1.5em;
      text-decoration: none;
      color: #0074D9;
      box-sizing: border-box;

    }
  }

  .underline::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 2px;
    background-color: #0074D9;
    transition: width 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  }

  @media (hover: hover) and (pointer: fine) {
    .underline:hover::before {
      left: 0;
      right: auto;
      width: 100%;
    }
  }
}
